import React from 'react'
import { Icon, Grid, Segment, Header } from 'semantic-ui-react'


const Result = ({ result }) => {
    
    if (!result) {
        return('')
    }
    

    //const result = ['Ei lakiperustetta', 'FINDATA']

    const style = { color: '#21ba45' }
    
    return(
        <Segment >
        <Grid textAlign='center'>
            <Grid.Row verticalAlign='middle'>
                <Icon name='law' size='huge'  style={style}/>
            </Grid.Row>
            <Grid.Row verticalAlign='middle'>
            
               <Header as='h3'>
                   {  result[0] }
                </Header>
            </Grid.Row>
            {
                createResultPermit(result)
            }
        </Grid>
             
        </Segment>
    )
    

}

const createResultPermit = result => {
    if (result.filter(res => res!=='').length < 2) {
        return('')
    } 
    const style = { color: '#21ba45' }

    let permitGiver = result[1]
    let permitType = ''
    if (permitGiver.match('/')) {
        const splitted = permitGiver.split('/')
        permitType = splitted[0].replace(/ {1,}$/,'')
        permitGiver = splitted[1].replace(/ {1,}$/,'')
    }

    console.log(permitGiver,permitType)

    return(
        <>
            <Grid.Row verticalAlign='middle'>
                <Icon name='gavel' size='huge' style={style} />
            </Grid.Row>
            <Grid.Row verticalAlign='middle'>
                <Header as='h3'>
                {  permitGiver }
                </Header>
            </Grid.Row>
            <Grid.Row verticalAlign='middle'>
                <Header as='h4'>
                {  permitType }
                </Header>
            </Grid.Row>
        </>
    )
}


export default Result